import styled from 'styled-components';

export const ProgressBarWrapper = styled.div`
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
`;

export const ProgressBarBall = styled.div`
  width: 20px;
  height: 20px;
  border-radius: 100%;
  background-color: ${({ theme, active }) =>
    active ? theme.colors.active : theme.colors.mainColorLight};
  transform: translateX(${({ translate }) => translate}px);
`;

export const ProgressBarMain = styled.div`
  width: 20%;
  height: 5px;
  background-color: ${({ theme }) => theme.colors.mainColorLight};
  position: relative;
  &::after {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: ${({ theme, full }) => full && theme.colors.active};
  }
`;
