import React from 'react';
import PropTypes from 'prop-types';
import {
  ProgressBarMain,
  ProgressBarBall,
  ProgressBarWrapper,
} from './ProgressBar.style';

const ProgressBar = ({ step }) => {
  return (
    <ProgressBarWrapper>
      <ProgressBarBall translate={2} active></ProgressBarBall>
      <ProgressBarMain full></ProgressBarMain>
      <ProgressBarMain full={step === 2 || step === 3}></ProgressBarMain>
      <ProgressBarMain full={step === 3}></ProgressBarMain>
      <ProgressBarBall translate={-2} active={step === 3}></ProgressBarBall>
    </ProgressBarWrapper>
  );
};

ProgressBar.propTypes = {};

export default ProgressBar;
