import styled from 'styled-components';

export const StyledLabel = styled.label`
  display: flex;
  height: ${({ height }) => height || 'auto'};
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  font-size: ${({ theme }) => theme.fontSize.xl};
  font-weight: 300;
  text-align: center;
`;
