import styled from 'styled-components';

export const RadioWrapper = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-evenly;
  margin-top: 50px;
  label > input:checked {
    color: red;
  }
  label {
    width: 45px;
    height: 45px;
    background-color: ${({ theme }) => theme.colors.mainColorLight};
    border-radius: 10px;
    font-size: ${({ theme }) => theme.fontSize.m};
  }
`;
